@value button-size: 20px;

.header {
  display: inline-block;
}

.collapse-button {
  height: button-size;
  transition: transform 0.3s;
  width: button-size;
  padding: 4px;
}

.collapse-button-open {
  transform: rotate(90deg);
}

.content {
  overflow-y: hidden;
}

.content-collapsed {
  height: 0;
}
