@value preview-background from '../../constants/colors.css';

.preview {
  background-color: preview-background;
  display: flex;
  flex: 1 1 auto;
  flex-flow: wrap;
  justify-content: space-around;
  overflow-x: auto;
  overflow-y: scroll;
}

@media (max-width: 600px) {

  .preview {
    flex: 1 0 100%;
  }
}

@media print {

  .preview {
    background-color: #ffffff;
    display: block;
    overflow-y: hidden;
  }
}
