@value controls-background from '../../constants/colors.css';
@value text-font from '../../constants/fonts.css';
.side-bar {
  display: flex;
  font-family: Droid Sans, sans-serif;
  background-color: controls-background;
  flex: 0 0 auto;
  flex-flow: column;
  max-width: 480px;
  overflow: hidden;
  position: relative;
  transition: 500ms;
  width: 100%;
}
@media (max-width: 600px) {
  .side-bar {
    max-width: 600px;
    left: -100%;
  }
  .side-bar.closed {
    left: 0;
  }
}
@media print {
  .side-bar {
    display: none;
  }
}
.controls {
  flex: 1 1;
  overflow-y: auto;
}
